
@charset "UTF-8";
* {
  -webkit-tap-highlight-color: transparent;
  outline: 0;
}
body, div, span, header, footer, nav, section, aside, article, ul, dl, dt, dd, li, a, p, h1, h2, h3, h4, h5, h6, i, b, textarea, button, input, select, figure, figcaption {
  padding: 0;
  margin: 0;
  font-style: normal;
  font-weight: normal;
  text-decoration: none;
  border: none;
  color: #303030;
  font-family: Helvetica,sans-serif;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
}
body:hover, div:hover, span:hover, header:hover, footer:hover, nav:hover, section:hover, aside:hover, article:hover, ul:hover, dl:hover, dt:hover, dd:hover, li:hover, a:hover, p:hover, h1:hover, h2:hover, h3:hover, h4:hover, h5:hover, h6:hover, i:hover, b:hover, textarea:hover, button:hover, input:hover, select:hover, figure:hover, figcaption:hover {
    outline: none;
}
img {
  border: 0 none;
  vertical-align: middle;
  width: 100%;
}
i, em {
  font-style: normal;
}
ol, ul {
  list-style: none;
}
input, select, button, h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-family: inherit;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background-color: #F5F5F5;
}

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px transparent;
  border-radius: 10px;
  background-color: #F5F5F5;
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}
input[type="button"], input[type="submit"], input[type="search"], input[type="reset"] {
  -webkit-appearance: none;
}
textarea {
  -webkit-appearance: none;
}
html {
  font-size: 20vw;
}
@media screen and (max-width: 320px) {
html {
      font-size: 64px;
}
}
@media screen and (min-width: 540px) {
html {
      font-size: 108px;
}
}
body {
  max-width: 540px;
  min-width: 320px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  font-size: 0.18667rem;
  font-family: -apple-system,Helvetica,sans-serif;
  line-height: 1.5;
  color: #666;
  -webkit-text-size-adjust: 100% !important;
}
.flex-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
}
.flex-container .flex-item {
    -webkit-box-flex: 1;
        -ms-flex: 1 0 auto;
            flex: 1 0 auto;
}
::-webkit-input-placeholder, .vux-popup-picker-placeholder {
  color: rgba(48, 48, 48, 0.3) !important;
}
.svg-icon {
  width: 0.26667rem;
  height: 0.26667rem;
  vertical-align: middle;
  fill: currentColor;
  overflow: hidden;
}
.cell {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0.2rem;
  line-height: 0.32rem;
  font-size: 0.24rem;
  font-weight: 700;
}
.cell .cell-fr {
    text-align: right;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin-right: 0.26667rem;
    font-size: 0.18667rem;
}
.cell .cell-fr.after:after {
      content: " ";
      display: inline-block;
      height: 6px;
      width: 6px;
      border-width: 2px 2px 0 0;
      border-color: rgba(48, 48, 48, 0.5);
      border-style: solid;
      -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
      transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
      position: absolute;
      top: 50%;
      margin-top: -4px;
      right: 0.2rem;
}

/*reset vux style*/
.weui-toast__content {
  color: #fff;
}
.weui-cells {
  font-size: 0.21333rem !important;
  margin-top: 0 !important;
}
.weui-cells:before {
    border: 0 none !important;
}
.weui-cells:after {
    border: 0 none !important;
}
.weui-cell {
  line-height: 0.42667rem;
  padding: 0.26667rem 0 0.13333rem !important;
  border-bottom: 1px solid #E6E6E6;
}
.weui-cell:before {
    border: 0 none !important;
}
.vux-cell-box:before {
  border: 0 none !important;
}

/*login*/
.mod-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0 0.2rem;
  background-color: #fff;
}
.mod-container .flexbox-header {
    padding-top: 0.26667rem;
}
.mod-container .site-title {
    font-size: 0.29333rem;
    font-weight: 700;
}
.mod-container .flexbox-guide {
    font-size: 0.21333rem;
    line-height: 0.42667rem;
    text-align: right;
}
.mod-container .icon-pwd {
    padding-right: 0.2rem;
}
.mod-container .icon-pwd.active {
      fill-opacity: 0.3;
}
.mod-container .weui-cells {
    margin-top: 0.2rem !important;
    margin-bottom: 0.4rem;
}
.mod-container .submit-btn {
    background-color: #FF3B30 !important;
}
.mod-container .smsCodeText {
    font-size: 0.18667rem;
}
.mod-container .forget-pwd {
    padding-top: 0.2rem;
}
.mod-container .forget-pwd a {
      color: rgba(48, 48, 48, 0.3);
}
.mod-container .text_agreement {
    padding-bottom: 0.2rem;
}
.mod-container .text_agreement a {
      color: #ff3b30;
}
.router-fade-enter-active, .router-fade-leave-active {
  transition: opacity .3s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}



